@import url(https://fonts.googleapis.com/css2?family=Noto+Sans&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Playfair+Display:wght@500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Inconsolata:wght@500&display=swap);
:root {
  --color-bg: white;
  --color-fg: #24292e;
  --color-dark: #26262f;
  --color-light: #f1f2f4;
  --color-gray: #c3c6d4;
  --color-accent: #866bd8;

  --syntax-keyword: #bae;
  --syntax-comment: #65737e;
  --syntax-literal: #b294bb;
  --syntax-bg: #101a29;
  --syntax-fg: #d4d6db;

  --font-sans: 'Noto Sans', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Ubuntu', 'Helvetica Neue', sans-serif;
  --font-mono: 'Inconsolata', source-code-pro, Menlo, Monaco, Consolas, monospace;
  --font-serif: 'Playfair Display', 'Merriweather', 'Times New Roman', serif;
}

body {
  margin: 0;
  font-family: 'Noto Sans', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Ubuntu', 'Helvetica Neue', sans-serif;
  font-family: var(--font-sans);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

p {
  color: black;
  line-height: 1.75em;
}

h1, h2, h3 {
  color: #24292e;
  color: var(--color-fg);
  font-family: 'Playfair Display', 'Merriweather', 'Times New Roman', serif;
  font-family: var(--font-serif);
}

code {
  font-family: 'Inconsolata', source-code-pro, Menlo, Monaco, Consolas, monospace;
  font-family: var(--font-mono);
  background: #f1f2f4;
  background: var(--color-light);
  padding: 2px;
  border-radius: 2px;
}

pre {
  line-height: 1.5em;
  padding: 24px;
  border-radius: 4px;
  color: #d4d6db;
  color: var(--syntax-fg);
  background: #101a29;
  background: var(--syntax-bg);
  overflow-x: auto;
}

pre code {
  background: none;
  padding: 0;
  border-radius: 0;
}

blockquote {
  margin: 0;
  padding: 12px;
  padding-left: 48px;
  border-radius: 2px;
  background: #f1f2f4;
  background: var(--color-light);
  border-left: 6px solid #c3c6d4;
  border-left: 6px solid var(--color-gray);
}

blockquote>p {
  color: #24292e;
  color: var(--color-fg);
  opacity: .95;
}

hr {
  border: 1px solid #f1f2f4;
  border: 1px solid var(--color-light);
}

img {
  max-width: 100%;
}


.header {
  margin-bottom: 16px;
}

.header-title {
  text-align: center;
  margin-bottom: 16px;
  font-size: 1.5em;
}

.header a {
  color: var(--color-fg);
}

.header h1 {
  font-family: var(--font-mono);
  margin: 12px 0;
}

.header a:hover {
  text-decoration: none;
}

.header-nav {
  padding-bottom: 16px;
  border-bottom: 1px solid var(--color-light);
}

.header-nav>ul{
  list-style: none;
  display: flex;
  justify-content: center;
  margin: 0;
  padding: 0;
}

.header-nav>ul>li {
  margin: 0 1em;
}

.header-nav a {
  padding-bottom: 1px;
}

.header-nav a:hover {
  text-decoration: none;
  border-bottom: 4px solid var(--color-accent);
}

.archive>ul {
  list-style: none;
  justify-content: center;
  margin: 0;
  padding: 0;
}

.archive-post {
  padding: 0 4px;
  padding-bottom: 12px;
  display: flex;
  flex-direction: column;
  border-bottom: 4px solid #00000000;
  color: var(--color-fg);
}

.archive-post:hover {
  border-bottom: 4px solid var(--color-gray);
  cursor: pointer;
  text-decoration: none;
}

.archive-post-name h3 {
  margin: 8px 0;
}

.archive-post-date {
  font-size: 0.75em;
}

/* Markdown */

.hljs-keyword {
  color: var(--syntax-keyword);
}

.hljs-literal, .hljs-number, .hljs-string {
  color: var(--syntax-literal);
}

.hljs-comment {
  color: var(--syntax-comment);
}

.post .header-anchor {
  display: none;
}

.post h2:hover .header-anchor {
  display: inline;
}

.post h3:hover .header-anchor {
  display: inline;
}

.post h4:hover .header-anchor {
  display: inline;
}

a {
  color: var(--color-accent);
  text-decoration: none;
}

a:hover {
  text-decoration: underline;
}

.app {
  display: flex;
  justify-content: center;
}

.container {
  width: 90%;
  margin-top: 16px;
  max-width: 720px;
}

