.archive>ul {
  list-style: none;
  justify-content: center;
  margin: 0;
  padding: 0;
}

.archive-post {
  padding: 0 4px;
  padding-bottom: 12px;
  display: flex;
  flex-direction: column;
  border-bottom: 4px solid #00000000;
  color: var(--color-fg);
}

.archive-post:hover {
  border-bottom: 4px solid var(--color-gray);
  cursor: pointer;
  text-decoration: none;
}

.archive-post-name h3 {
  margin: 8px 0;
}

.archive-post-date {
  font-size: 0.75em;
}

/* Markdown */

.hljs-keyword {
  color: var(--syntax-keyword);
}

.hljs-literal, .hljs-number, .hljs-string {
  color: var(--syntax-literal);
}

.hljs-comment {
  color: var(--syntax-comment);
}

.post .header-anchor {
  display: none;
}

.post h2:hover .header-anchor {
  display: inline;
}

.post h3:hover .header-anchor {
  display: inline;
}

.post h4:hover .header-anchor {
  display: inline;
}
